import { defineComponent } from "vue";
export default defineComponent({
  props: {
    listData: {
      type: Array,
      require: true
    },
    listCount: {
      type: Number,
      require: true
    },
    propList: {
      type: Array,
      require: true
    },
    showIndexColumn: {
      type: Boolean,
      default: false
    },
    showSelectColumn: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    },
    page: {
      type: Object,
      default: () => ({
        curenntPage: 0,
        pageSize: 10
      })
    },
    childrenProps: {
      type: Object,
      defalut: () => ({})
    },
    showFooter: {
      type: Boolean,
      defalut: true
    }
  },
  emits: ["selectionChange", "update:page"],

  setup(props, {
    emit
  }) {
    const handleSelectionChange = value => {
      emit("selectionChange", value);
    };

    const handleCurrentChange = currentPage => {
      emit("update:page", { ...props.page,
        currentPage
      });
    };

    const handleSizeChange = pageSize => {
      emit("update:page", { ...props.page,
        pageSize
      });
    };

    return {
      handleSelectionChange,
      handleCurrentChange,
      handleSizeChange
    };
  }

});