import { defineComponent, computed, ref, watch } from "vue";
import { useStore } from "@/store";
import AcTable from "@/base-ui/table/src/AcTable.vue";
import excludeSlotNames from "./config/exclude-solt";
import { usePermission } from "@/hooks/usePermission";
export default defineComponent({
  props: {
    contentTableConfig: {
      type: Object,
      require: true
    },
    pageName: {
      type: String,
      require: true
    }
  },
  components: {
    AcTable
  },
  emits: ["selectChange", "createBtnClick", "editBtnClick"],

  setup(props, {
    emit
  }) {
    const store = useStore(); // 获取操作权限

    const isCreate = usePermission(props.pageName, "create");
    const isUpdate = usePermission(props.pageName, "update");
    const isDelete = usePermission(props.pageName, "delete");
    const isQuery = usePermission(props.pageName, "query"); // 双向绑定pageInfo

    const pageInfo = ref({
      currentPage: 1,
      pageSize: 10
    });
    watch(pageInfo, () => {
      getPageData();
    }); // 发送网络请求

    const getPageData = (queryInfo = {}) => {
      if (!isQuery) return;
      store.dispatch("system/getPageListAction", {
        pageName: props.pageName,
        queryInfo: {
          offset: (pageInfo.value.currentPage - 1) * pageInfo.value.pageSize,
          size: pageInfo.value.pageSize,
          ...queryInfo
        }
      });
    };

    getPageData(); //vuex 中获取数据

    const dataList = computed(() => store.getters[`system/pageListData`](props.pageName));
    const listCount = computed(() => store.getters[`system/pageListCount`](props.pageName));

    const selectChange = value => {
      emit("selectChange", value);
    }; // 4. 获取其他的动态插槽名称


    const otherPropSlots = props.contentTableConfig?.propList.filter(item => {
      if (excludeSlotNames.includes(item.slotName)) return false;
      return true;
    }); // 5. 添加/删除/修改

    const handleDeleteClick = item => {
      store.dispatch("system/deletePageDataAction", {
        pageName: props.pageName,
        id: item.id
      });
    };

    const handleCreateClick = () => {
      emit("createBtnClick");
    };

    const handleEditClick = item => {
      emit("editBtnClick", item);
    };

    return {
      selectChange,
      getPageData,
      handleDeleteClick,
      handleCreateClick,
      handleEditClick,
      dataList,
      listCount,
      pageInfo,
      otherPropSlots,
      isCreate,
      isUpdate,
      isDelete,
      isQuery
    };
  }

});